h1{
    padding-bottom: 5px;
    margin: 0%;
}
.conatiner{
    width: 400px;
    height: 500px;
    background-color: black;
    color: white;
}
.input-container{
    width: 400px;
    margin-top: 20px;
    display: flex;
    justify-content: center;
    gap: 30px;
}
.input-container input{
    width: 200px;
    height: 50px;
    border: none;
    outline: none;
    font-size: 25px;
    padding-left: 10px;
    border-radius: 30px;
}
.input-container button{
    width: 100px;
    border-radius: 20px;
    font-size: 20px;
}
.image{
    width: 400px;
    height: 300px;
    /* background-color: aqua; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
img{
    width: 200px;
    height: 200px;
    margin: 0%;
}
h2{
    margin: 0%;
    padding: 0%;
}
.container1{
    width: 400px;
    height: 130px;
    /* background-color: blue; */
    display: flex;
    justify-content: center;
    gap: 30px;
}
.wind{
    width: 150px;
    height: 130px;
    /* background-color: blueviolet; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.humidity{
    width: 150px;
    height: 130px;
    /* background-color: blueviolet; */
    display: flex;
